/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 10px;
    width: auto;
    height: 22px;
    line-height: 22px;
    margin: 0;
    padding: 1px 12px 0;
    box-sizing: border-box;
    @include mkdfBorderRadius(3px);
    font-family: $default-heading-font;
    color: $default-heading-color;
    font-size: 13px;
    font-weight: 600;
    letter-spacing: -.07em;
    text-align: center;
    z-index: 100;
    @include mkdfTransform(translateZ(0));
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 10px;
    background-color: $second-main-color;
}

@mixin mkdfWooCommerceOutOfStockyStyle() {
    right: 10px;
    background-color: $second-main-color;
}
/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    font-size: 18px;
    margin: 9px 0 0;
    line-height: 1;
    color: $first-main-color;
    font-family: $default-heading-font;
    font-weight: 600;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 2px;
    color: #fad818;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */