/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    margin: 27px 0 70px;
    clear: both;
    border-top: 1px solid $default-border-color;
    padding-top: 18px;
    box-sizing: border-box;

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        padding: 0;
        margin: 0 5px;
        text-decoration: none;
        cursor: pointer;
        white-space: nowrap;
        color: $default-text-color;
        @include mkdfTransition(color .2s ease-in-out, border-color .2s ease-in-out);
        box-sizing: border-box;
        border: 1px solid transparent;
        line-height: 20px;
        height: 20px;
        width: 20px;

        &:hover,
        &.current {
            color: $first-main-color;
            border-color: $first-main-color;
        }

        &.next,
        &.prev {
            font-size: 0;
            line-height: 1;
            margin: 0;
            position: absolute;
            border: 1px solid $default-border-color;

            &:before {
                position: relative;
                font-family: 'Ionicons';
                font-size: 12px;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                height: 100%;
                line-height: 20px;
                display: inline-block;
                width: 100%;
            }
            &:hover {
                border-color: $first-main-color;
            }
        }

        &.prev {
            left: 0;
            &:before {
                content: "\f3d2";
            }
        }

        &.next {
            right: 0;
            &:before {
                content: "\f3d3";
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */