/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 64px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0;
			    box-sizing: border-box;
                border: 1px solid $default-border-color;
			    
			    &:first-child {
				    left: 0;
				    width: 100%;
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    margin-top: 15px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				    $calc_width: 30px/($i+2);
                    $margin_width: 30px/($i+1);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: calc(#{$column_width} - #{$calc_width});
                            margin-right: $margin_width;
                            margin-top: $margin_width;
					    }
                        &:nth-child(#{$i+2}n+#{$i+3}) {
                            margin-right: 0;
                        }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 3px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 17px;
			padding-bottom: 27px;
			border-bottom: 1px solid $default-border-color;
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 16px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 29px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
				font-weight: 600;
				color: #000;

                span {
                    font-weight: 300;
                    color: $default-heading-color;
                }

			    a {
					font-weight: 300;
				    color: $default-heading-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		    margin: 24px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 30px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 1px solid $default-border-color;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
                padding: 13px 30px 13px 0;

				&:first-child {
					a {
						padding-left: 0;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					font-weight: 600;

					&:hover {
						color: $default-heading-color;
					}
                    &:after {
                        content: "";
                        border-bottom: 2px solid $default-heading-color;
                        display: inline-block;
                        width: 100%;
                        opacity: 0;
                        height: 0;
                        line-height: 0;
                        position: absolute;
                        bottom: -14px;
                        left: 0;
                        @include mkdfTransition(opacity 0.2s ease-in-out);
                    }
				}
                &.active,
                &:hover {
                    a:after {
                        opacity: 1;
                    }
                }
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 15px 0 20px;
			}
		}
		
		table {
			margin: 23px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
                color: $default-heading-color;
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 40px 0 0;
		border-top: 1px solid $default-border-color;
		
		> h2 {
			display: block;
			margin: 50px 0 14px;
			text-align: left;
			font-size:18px;

		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */